import React ,{ useState , useEffect} from 'react'; 
import { FaBars, FaTimes } from 'react-icons/fa';
import { IconContext } from 'react-icons/lib';
import { Button } from '../../globalStyles';
import {
    Nav, NavbarContainer, NavIcon, NavLogo, MobileIcon, NavItem, NavLinks, NavMenu, NavBtnLink, NavItemBtn
    , Logo
} from './Navbar.elements';
import logo from '../../images/logo.png';



//Reponsive Navbar
const Navbar = () => {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);
    const handleClick = () => setClick(!click);



 
   
  


    //Animation button
    const showButton = () => {
        if (window.innerWidth <= 960) {
            setButton(false)
        } else {
            setButton(true)
        }
    };
    //set react hook
    useEffect(() => {
        showButton();
    }, []);

    window.addEventListener('resize', showButton);


    

   
    
//endReponsiveNavbar
    return (
        <>
            <IconContext.Provider value={{ color: '#fff'}}>        
                <Nav>
                <NavbarContainer>
                    <NavLogo to="/">
                            {/* <NavIcon />   */}
                              <Logo img src={logo} />
                        </NavLogo>
                    <MobileIcon onClick={handleClick}>{click ? <FaTimes /> : <FaBars />}</MobileIcon>
                        <NavMenu onClick={handleClick} click={click}>
                            <NavItem>
                                <NavLinks to='/'>
                                     หน้าหลัก
                                </NavLinks>
                             </NavItem>                        
                            <NavItem>
                                <NavLinks to='/Services'>
                                    ข่าวสาร
                                </NavLinks>
                             </NavItem>
                            <NavItem>
                                <NavLinks to='/Products'>
                                   วัด
                                </NavLinks>
                            </NavItem>
                            <NavItem>
                                <NavLinks to='/Others'>
                                   ฝ่ายต่างๆ
                                </NavLinks>
                            </NavItem>
                            <NavItem>
                                <NavLinks to='/About'>
                                   เกี่ยวกับเรา
                                </NavLinks>
                            </NavItem>
                            <NavItem>
                                <NavLinks to='/Search'>
                                   ค้นหา
                                </NavLinks>
                            </NavItem>
                            <NavItemBtn>
                                {button ? (
                                    <NavBtnLink to="/SignUp">
                                        <Button primary>ที่อยู่</Button>
                                    </NavBtnLink>
                                ) : (
                                        <NavBtnLink to="/SignUp">
                                            <Button  fontBig primary> ที่อยู่</Button>
                                        </NavBtnLink>
                                )}
                            </NavItemBtn>
                        </NavMenu>
                    </NavbarContainer>
                </Nav>       
                </IconContext.Provider>
        </>
    )
}

export default Navbar ;
